<template>
    <div class="menu">
        <Menu mode="horizontal" :theme="theme" active-name="0">
            <MenuItem class="menu-item" v-for="(item, index) in menuData" :key="index" :name="index" :title="item.text">
                <span class="menu-item-span" @click="changeSpace(item)">
                    <Icon :type="item.iconType" size="24"/>
                    {{$t(item.text)}}
                </span>
            </MenuItem>
        </Menu>
    </div>
</template>
<script>
export default {
    name: "VMenu",
    props:["theme"],
    data(){
        return{
            menuData:[]
        }
    },
    created(){
        this.init();
    },
    methods:{
        init(){
            //假数据 //仮データ //fake data
            this.menuData = [
                {
                    iconType: "ios-home-outline",
                    text: "nav.menu.working",
                    route: "/kalkin/working"
                },
                {
                    iconType: "ios-book-outline",
                    text: "nav.menu.learning",
                    route: "/kalkin/learning"
                },
                {
                    iconType: "ios-cafe-outline",
                    text: "nav.menu.living",
                    route: "/kalkin/living"
                },
                {
                    iconType: "ios-game-controller-a-outline",
                    text: "nav.menu.gaming",
                    route: "/kalkin/gaming"
                },
                {
                    iconType: "ios-settings-outline",
                    text: "nav.menu.management",
                    route: "/kalkin/management"
                }
            ]
        },
        changeSpace(data){
            if(this.$route.path != data.route){
                this.$router.push(data.route);
            }
        }
    }
}
</script>
<style>
    .menu .menu-item{
        max-width: 150px;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        word-break: break-all;
    }
    .menu .menu-item-span{
        display: inline-block;
        height: 100%;
    }
</style>