<template>
    <div class="index-page-module">
        <div class="space-wrapper" @click="clickCard">
            <Card v-show="tabIndex === 0" :style="setBackColor()">
                <p class="card-title" slot="title" :title="cardData.title">{{cardData.title}}</p>
                <Row class="space-module code-row-bg" type="flex" justify="center">
                    <Col span="4">
                        <div class="space-desc">
                            {{cardData.desc}}
                        </div>
                    </Col>
                    <Col span="16">
                        <div class="img-split">
                            <img v-for="(img, i) in cardData.imgMsg" :key="i" :src="img.path" :alt="$t('error.imgErrorMsg')">
                        </div>
                    </Col>
                    <Col span="4">
                        <div class="other-msg">
                            {{cardData.otherMsg}}
                        </div>
                    </Col>
                </Row>
            </Card>
        </div>
        <div class="card-theme-wrapper" @click="clickCard">
            <Col v-show="tabIndex === 1" class="card-theme" span="4">
                <Card>
                    <p class="card-title" slot="title">{{cardData.title}}</p>
                    <div class="cardThemeContent">
                        <p class="title">{{cardData.poem.poemTitle}}</p>
                        <div class="text">{{cardData.poem.text}}</div>
                    </div>
                </Card>
            </Col>
        </div>
    </div>
</template>
<script>
export default {
    name: "VIndexPageModule",
    data(){
        return{
            cardData: {}
        }
    },
    props:{
        "moduleId": {
            type: String,
            required: true
        },
        "tabIndex": {
            type: Number,
            required: true
        },
        "k": {
            type: Number,
            required: false
        }
    },
    mounted(){
        this.init();
    },
    methods: {
        init(){
            //TODO: Request for data according to the moduleId .. // fake data
            this.cardData = {
                title: "生活空间",
                route: "/kalkin/learning",
                desc: `
        人生聚散离合，就是缘来缘尽， 生活是一件艺术品， 每个人都有自己认为最美的一笔， 每个人也都有认为不尽如人意的一笔， 人没有最好， 有德天地高， 人生有尺，做人有度。 
        淡淡的日子淡然地过。生活如水，人生似茶，再好的茶放到水中一泡，时间久了，也就淡了。也许是棱角平了，或许是成熟稳重了，脚步越来越踏实，日子越来越平淡。
                `,
                imgMsg: [
                    {
                        path: require('assets/img/card_1.png')
                    },
                    {
                        path: require('assets/img/card_2.png')
                    },
                    {
                        path: require('assets/img/card_3.png')
                    },
                    {
                        path: require('assets/img/card_4.png')
                    },
                    {
                        path: require('assets/img/card_5.png')
                    }
                ],
                otherMsg: "this is a context about other message.",
                poem: {
                    poemTitle: "南歌子·云鬓裁新绿",
                    text: `云鬓裁新绿，霞衣曳晓红。 待歌凝立翠筵中，一朵彩云何事、下巫峰。 
趁拍鸾飞镜，回身燕漾空。莫翻红袖过帘栊，怕被杨花句引、嫁东风。`
                }
            }
        },
        clickCard(){
            this.$router.push({ path: this.cardData.route });
        },
        setBackColor(){
            return { background: this.k % 2 ? "linear-gradient(to right, white, lightblue)" : "linear-gradient(to right, lightblue, white)" };
        }
    }
}
</script>
<style>
    .index-page-module{
        margin: 10px 20px;
        cursor: pointer;
    }
    .index-page-module .space-module{
        height: 30vh;
    }
    .index-page-module .space-module .ivu-col-span-16{
        border: 1px solid #eee;
        border-top: 0;
        border-bottom: 0;
    }
    .index-page-module .space-module .ivu-col{
        padding: 10px 20px;
    }
    .index-page-module .space-module .space-desc{
        white-space: pre-wrap;
        text-align: left;
    }
    .index-page-module .card-theme{
        padding: 10px;
    }
    .index-page-module .card-theme .ivu-card{
        height: 30vh;
    }
    .index-page-module .card-title{
        color: #3cc;
    }
    .index-page-module .space-module .img-split{
        height: 100%;
    }
    .index-page-module .space-module .img-split>img{
        display: block;
        float: left;
        width: 20%;
        height: 100%;
        border-radius: 50%;
        box-sizing: border-box;
        padding: 10px;
        object-fit: cover;
        background: azure;
        transition: transform .2s linear;
    }
    .index-page-module .space-module .img-split>img:hover{
        transform: scale(1.1);
    }
    .index-page-module .cardThemeContent .title{
        padding-bottom: 20px;
        font-size: 20px;
        font-weight: bold;
    }
    .index-page-module .cardThemeContent .text{
        white-space: pre-wrap;
    }
</style>