<template>
    <div class="search">
        <Input v-model="val" :placeholder="$t('nav.search.seachTip')" style="width: auto">
            <Icon type="ios-search" color="#fff" slot="suffix" @click="search"/>
        </Input>
    </div>
</template>
<script>
export default {
    name: "VSearch",
    data(){
        return{
            val: ""
        }
    },
    methods:{
        search(){
            if(!this.val){
                this.$Message.warning(this.$t("nav.search.searchWarningMsg"));
                return;
            }
            //TODO: search action
        }
    }
}
</script>
<style>
.search{
    width: 200px;
}
.search .ivu-input-suffix{
    background:#2d8cf0;
    border-radius:0 4px 4px 0;
}
</style>