<template>
  <div class="header-layout-default">
    <v-logo :img="url"></v-logo>
    <v-search></v-search>
    <v-menu :theme="theme"></v-menu>
    <v-user></v-user>
    <v-lang></v-lang>
  </div>
</template>
<script>
import VLogo from "components/headerComponent/VLogo";
import VSearch from "components/headerComponent/VSearch";
import VMenu from "components/headerComponent/VMenu";
import VLang from "components/headerComponent/VLang";
import VUser from "components/headerComponent/VUser";
export default {
  name: "HeaderLayoutDefault",
  components: { VLogo, VSearch, VMenu, VLang, VUser },
  data() {
    return {
      url: require("@/assets/img/logo.png"),
      isAction: false,
      theme: "light"
    };
  }
};
</script>
<style>
    .header-layout-default{
      height: 100%;
      width: 100%;
      text-align: left;
    }
    .header-layout-default>div{
      position: relative;
      display: inline-block;
      height: 100%;
      margin: 0 10px;
    }
    .header-layout-default>div>div{
      position: absolute;
      top: 50%;
      left: 0;
      right: 0;
      transform: translateY(-50%);
    }
    .header-layout-default+div{
      min-height: calc(100vh - 60px);
      background: aliceblue;
    }
</style>