import Vue from 'vue';
import VueI18n from 'vue-i18n';
Vue.use(VueI18n);

import en from './config/en.js';
import zh from './config/zh.js';
import ja from './config/ja.js';

import {getLang} from '@/util'

const i18n = new VueI18n({
    locale: getLang(),
    messages: {
        en, zh, ja
    },
    silentTranslationWarn: true
})

export default i18n;