<template>
  <div class="logo" :title="logoTitle">
    <img @click="doAction" :src="img" :alt="$t('error.imgErrorMsg')"/>
  </div>
</template>
<script>
export default {
  name: "VLogo",
  data() {
    return {
      logoTitle: "",
      img: "",
      indexUrl: "/kalkin/index"
    };
  },
  props: {
    action: {
      type: Boolean,
      default: true
    }
    //img: {
      //type: String,
      //required: false,
      //default: require("assets/img/defuser2.jpg")
    //}
  },
  mounted(){
    this.logoTitle = this.$t('nav.logo.logoTitle');
    this.img = require('assets/img/defuser2.jpg');
  },
  methods: {
    doAction() {
      if (this.action && this.$router.currentRoute.path != this.indexUrl) {
        this.$router.push({ path: "/" });
      }
    }
  }
};
</script>
<style>
    div.logo{
        height: 100%;
        width: 60px;
        margin-left: 50px;
        cursor: pointer;
    }
    .logo img{
        display: inline-block;
        height: 100%;
    }
</style>