<template>
    <div class="user">
        <div class="user-wrapper" @click="openDrawer">
            <!-- <Avatar style="background-color: #5cadff" icon="ios-person" size="24"/> -->
            <Icon type="ios-contact-outline" color="#5cadff" size="24"/>
            <span class="user-text" :title="$t('nav.user.userTitle')">{{$t('nav.user.userTitle')}}</span>
        </div>
        <Drawer class="user-drawer" :title="$t('nav.user.userTitle')" draggable scrollable closable v-model="showDrawerFlag" on-close="closeDrawer" width="20">
            <div class="user-img-row">
                <img :src="userImg" :alt="$t('error.imgErrorMsg')"/>
                <!-- TODO: upload action -->
                <Upload class="upload-user-comp" action="">
                    <Button class="upload-user-btn" size="small" icon="ios-cloud-upload-outline" :title="$t('nav.user.changeAvatar')">
                        {{$t('nav.user.changeAvatar')}}
                    </Button>
                </Upload>
                <div class="user-name" :title="userName">{{userName}}</div>
            </div>
            <div class="user-favorite">
                <p class="title">{{$t('nav.user.favorite')}}</p>
                <li class="user-favor-list" v-for="(item, key) in favorList" :key="key">
                    <Button type="info" :title="item[lang]">{{item[lang]}}</Button>
                </li>
            </div>
            <div class="user-grade">
                <p class="title">{{$t('nav.user.grade')}}</p>
                <div class="star-area" ref="userStarAreaEl">
                    <li class="star" v-for="i in [1,2,3,4,5]" :key="i"><Icon size=24 type="ios-star-outline"></Icon></li>
                    <div class="gradeNumber">{{userGrade}}</div>
                    <div class="star-area-fullfill" :style="{width: Math.floor(userGrade) * (userStarAreaWith/5) + (userStarAreaWith/5-24)/2 + 24 * (userGrade*10%10/10) +'px'}">
                        <li class="star" v-for="i in [1,2,3,4,5]" :key="i"><Icon size=24 type="ios-star"></Icon></li>
                    </div>
                </div>
                <div class="ranking">{{$t('nav.user.ranking')}}{{rankingNumber}}</div>
            </div>
            <div class="user-login">
                <Button long type="primary" @click="loginoutAction()" style="margin-bottom:5px;">{{loginoutMsg}}</Button>
                <Button long type="warning" @click="openLoginModalFlag = true">{{$t('common.changeAccount')}}</Button>
                <Modal v-model="openLoginModalFlag" draggable :title="$t('common.login')">
                    <Form ref="formRegister" :model="formRegisterData" :rules="ruleInline" label-position="left" :label-width="100">
                        <FormItem prop="userName" :label="$t('common.userName')">
                            <Input type="text" v-model="formRegisterData.userName">
                                <Icon type="ios-person-outline" slot="prepend"></Icon>
                            </Input>
                        </FormItem>
                        <FormItem prop="password" :label="$t('common.password')">
                            <Input type="password" v-model="formRegisterData.password">
                                <Icon type="ios-lock-outline" slot="prepend"></Icon>
                            </Input>
                        </FormItem>
                    </Form>
                    <div slot="footer">
                        <Button @click="openLoginModalFlag = false">{{$t('common.cancel')}}</Button>
                        <Button type="primary" @click="handleSubmit()">{{$t('common.comfirm')}}</Button>
                    </div>
                </Modal>
            </div>
        </Drawer>
    </div>
</template>
<script>
export default {
    name:"VUser",
    data(){
        return{
            showDrawerFlag: false,
            userImg: "",
            userName: "",
            favorList: [],
            lang: "",
            userGrade: 0,
            userStarAreaWith: 0,
            rankingNumber: 0,
            loginoutMsg: "",
            openLoginModalFlag: false,
            formRegisterData: {},
            ruleInline: {
                user: [
                    { required: true, message: this.$t('error.userNameNoFillMsg'), trigger: 'blur' }
                ],
                password: [
                    { required: true, message: this.$t('error.passwordNoFillMsg'), trigger: 'blur' },
                    { type: 'string', min: 6, message: this.$t('error.passwordFillWrongMsg'), trigger: 'blur' }
                ]
            }
        }
    },
    created(){
        this.lang = this.$cookies.get("lang") || window.navigator.language.substring(0,2).toLowerCase();
        if(this.lang !== "zh" && this.lang !== "ja"){
            this.lang = "en";
        }
    },
    mounted(){
        this.init();
    },
    methods: {
        init(){
            //TODO:fake axios response data
            this.userName = this.$cookies.get("username") || '';
            this.loginoutMsg = this.userName.length ? this.$t('common.logout') : this.$t('common.login');
            this.userImg = require("assets/img/defuser1.jpg");
            this.favorList = [
                {
                    en: "Traval",
                    zh: "旅游",
                    ja: "旅行"
                },
                {
                    en: "Movie",
                    zh: "电影",
                    ja: "映画"
                },
                {
                    en: "Learning",
                    zh: "学习",
                    ja: "勉強"
                },
                {
                    en: "Learning",
                    zh: "学习",
                    ja: "勉強"
                },
                {
                    en: "Learning",
                    zh: "学习",
                    ja: "勉強"
                },
                {
                    en: "Learning",
                    zh: "学习",
                    ja: "勉強"
                }
            ],
            this.userGrade = 2.6;
            this.rankingNumber = 1723;
        },
        openDrawer(){
            this.showDrawerFlag = !this.showDrawerFlag;
            const _this = this;
            setTimeout(function(){
                _this.userStarAreaWith = _this.$refs.userStarAreaEl.offsetWidth - 30;
            },200);
        },
        closeDrawer(){
            this.showDrawerFlag = false;
        },
        loginoutAction(){
            //TODO: axios - ajax
            if(this.userName.length){
                this.$cookies.set("username", "");
                window.location.reload();
            }else{
                this.openLoginModalFlag = true;
            }
        },
        handleSubmit(){
            console.log(this.formRegisterData);
            //TODO: axios - ajax
            this.$cookies.set("username", this.formRegisterData.userName);
            this.openLoginModalFlag = false;
            window.location.reload();
        }
    }
}
</script>
<style>
    div.user{
        float: right;
        width: 120px;
        margin-right: 50px;
    }
    .user:hover{
        background: ghostwhite;
    }
    .user .user-wrapper{
        padding: 0 10px;
        color: dodgerblue;
        cursor: pointer;
    }
    .user .user-wrapper .user-text{
        display: inline-block;
        position: absolute;
        left: 38px;
        max-width: 78px;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        word-break: break-all;
    }
    .user-drawer .user-img-row{
        margin: 15px 0;
    }
    .user-drawer .user-img-row img{
        display: block;
        width:200px;
        height: 200px;
        margin: 10px auto;
        border-radius: 50%;
    }
    .user-drawer .upload-user-comp{
        position: absolute;
        top: 190px;
        right: 10px;
    }
    .user-drawer .upload-user-comp .upload-user-btn{
        max-width: 100px;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        word-break: break-all;
    }
    .user-drawer .user-name{
        text-align: center;
        font-size: 24px;
        color: darkturquoise;
    }
    .user-drawer .title{
        font-size: 20px;
        font-weight: bold;
        font-style: italic;
        padding: 15px 0;
    }
    .user-drawer .user-favorite .user-favor-list{
        display: inline-block;
        margin: 0 0 10px 15px;
    }
    .user-drawer .user-grade .star-area{
        display: inline-block;
        position: relative;
        padding: 0 15px;
    }
    .user-drawer .user-grade .star{
        display: inline-block;
        color: #f5a623;
    }
    .user-drawer .user-grade{
        position: relative;
    }
    .user-drawer .user-grade .gradeNumber{
        position: absolute;
        right: -15px;
        top: 2px;
        color: #f5a623;
    }
    .user-drawer .star-area-fullfill{
        position: absolute;
        bottom: 0;
        overflow: hidden;
        white-space: nowrap;
    }
    .user-drawer .ranking{
        padding: 10px 15px;
        color:lightskyblue;
    }
</style>