<template>
    <div class="lang" @mouseover="mouseOverLang" @mouseleave="mouseLeaveLang">
        <div class="lang-wrapper">
            <Icon type="md-globe" color="#5cadff" size="24"/>
            <span class="lang-text">{{curLang}}</span>
        </div>
        <div class="lang-option" v-show="showLangOptFlag">
            <li class="lang-selection" v-for="(item,key) in langOpt" :key="key" @click="changeLang(item)">
                <Icon type="ios-flower-outline" color="#5cadff" size="24"/>
                <span class="lang-sel-text">{{item.split("/")[1]}}</span>
            </li>
        </div>
    </div>
</template>
<script>
export default {
    name: "VLang",
    data(){
        return{
            curLang: "",
            langOpt: ["en/English", "zh/中文", "ja/日本語"],
            showLangOptFlag: false
        }
    },
    created(){
        let windowLang = window.navigator.language.substring(0,2).toLowerCase();
        if(windowLang !== "zh" && windowLang !== "ja"){
            windowLang = "en";
        }
        let lang = ((this.$cookies.get("lang") || windowLang || langOpt[0]).substring(0,2)).toLowerCase();
        if(!this.$cookies.get("lang")){
            this.$cookies.set("lang", lang);
        }
        this.curLang = "English";
        let _this = this;
        this.langOpt.forEach(el => {
            if(el.substring(0,2).toLowerCase() === lang){
                _this.curLang = el.split("/")[1];
            }
        });
    },
    methods: {
        mouseOverLang(){
            this.showLangOptFlag = true;
        },
        mouseLeaveLang(){
            this.showLangOptFlag = false;
        },
        changeLang(lang){
            this.showLangOptFlag = false;
            this.curLang = lang.split("/")[1];
            this.$i18n.locale = lang.split("/")[0];
            // this.$store.dispatch("updateLang", {lang: lang.split("/")[0]});
            this.$store.commit("updateLang", {lang: lang.split("/")[0]});
            // window.location.reload();//更新后台数据语言， 对i18n国际化没影响
        }
    },
    watch:{
        
    }
}
</script>
<style>
    div.lang{
        float: right;
        width: 120px;
        text-align: left;
    }
    .lang:hover{
        background: ghostwhite;
    }
    .lang .lang-wrapper{
        left:10px;
        color: dodgerblue;
    }
    .lang .lang-option{
        position: absolute;
        top: 115px;
        left: 0;
        z-index: 100;
        width: 100%;
        padding: 10px 0;
        background: ghostwhite;
        border-top: 1px solid cornflowerblue;
    }
    .lang-option .lang-selection{
        position: relative;
        height: 30px;
        cursor: pointer;
    }
    .lang-option .lang-selection:hover{
        background: lavender;
    }
    .lang-option .lang-selection i{
        position: absolute;
        top: 3px;
        left: 10px;
    }
    .lang-option .lang-selection .lang-sel-text{
        position: absolute;
        top: 3px;
        left: 40px;
    }
</style>